// 用于保存计算&debug等方法
export default {
    methods: {
        handleAxisData(dataArr) {
            dataArr = dataArr.reverse();
            let distance = 0;
            dataArr.forEach((p, i) => {
                const prev = dataArr[i-1];
                if (prev) {
                    const d = this.calcDistance(prev, p);
                    if (d < 1) return;
                    distance += d;
                }
                const obj = {
                    d: distance,
                    p,
                };
                this.data.coordArr.push(obj);
            });
            this.data.totalLength = distance;
        },
        queryPositionData(arr) { // 传入隧道长度坐标，返回该点的空间坐标和向量等
            // 二分法找到距离指定位置前的一个坐标点，计算出该位置空间坐标和所在向量。
            const res = [];
            const _this = this;
            let max;
            let min;
            function calcDetail(max, min, distance) {
                const minData = _this.data.coordArr[min];
                const maxData = _this.data.coordArr[max];
                const v = _this.calcVector(maxData.p, minData.p, true);
                const d = distance - minData.d;
                const obj = {
                    v,
                    p: {
                        x: minData.p.x - v.x * d,
                        y: minData.p.y - v.y * d,
                        z: minData.p.z - v.z * d,
                    },
                };
                res.push(obj);
            }
            function dichotomic(distance) {
                if (max - min === 1) {
                    calcDetail(max, min, distance);
                    return;
                }
                const mid = Math.floor((max + min) / 2);
                const midData = _this.data.coordArr[mid];
                if (midData.d > distance) {
                    max = mid;
                } else {
                    min = mid;
                }
                dichotomic(distance);
            }
            arr.forEach((distance) => {
                min = 0;
                max = this.data.coordArr.length - 1;
                dichotomic(distance);
            });
            return res;
        },
        addTestPoints(p) {
            const material = new THREE.SpriteMaterial({ color: 0xDC143C });
            const sprite = new THREE.Sprite(material);
            sprite.position.set(p.x, p.y, p.z);
            sprite.scale.set(14, 14, 14);
            return sprite;
        },
        linstenClick(objList) {
            this.listenEvent('click', this.camera, objList, (targetList) => {
                if (!targetList.length) return;
                const target = targetList[0];
                console.log(target);
                // if (this.prevTarget) {
                //     const distance = this.distance(target.point, this.prevTarget.point);
                //     console.log(distance);
                // } else {
                //     this.prevTarget = target;
                // }
            });
        },
    },
};