import 'three/examples/js/loaders/OBJLoader.js';
import 'three/examples/js/loaders/MTLLoader.js';

const { PI } = Math;
export default {
    methods: {
        loadModel(modelName) {
            return new Promise((resolve) => {
                const mtlLoader = new THREE.MTLLoader();
                const objLoader = new THREE.OBJLoader();
                mtlLoader.load(`./model/${modelName}.mtl`, (mat) => {
                    mat.preload();
                    objLoader.setMaterials(mat);
                    objLoader.load(`./model/${modelName}.obj`, (obj) => {
                        resolve(obj);
                    });
                });
            });
        },
        addObject(objName, params) {
            const { num, interval, intervalOffset, offsetX, offsetY, scale, start, end, twice } = params;
            const objModel = this.loadModel(objName);
            const group = new THREE.Group();
            group.name = `${objName}Group`;
            
            const distanceList = [];
            for (let i = 0; i < num; i++) {
                distanceList.push((i + 1) * interval + (intervalOffset || 0));
            }
            
            if (start !== undefined) distanceList.unshift(start);
            if (end !== undefined) distanceList.push(end);
            
            const positionList = this.queryPositionData(distanceList);

            let horizontal;
            objModel.then(async (obj) => {
                if (scale) obj.scale.set(scale, scale, scale);
                switch (objName) {
                    case 'lamp':
                        obj.rotation.z = - PI / 4.5;
                        break;
                    case 'hydrant':
                        obj.children[0].rotation.y = PI;
                        break;
                    case 'fan-shell':
                        obj.children[0].position.set(-0.04, 0.15, 0);
                        const fanLeaf = await this.loadModel('fan-leaf');
                        fanLeaf.position.set(-0.04, -0.02, -1.2);
                        obj.add(fanLeaf);
                        const fanLeafClone = fanLeaf.clone();
                        fanLeafClone.position.z = 1.2;
                        obj.add(fanLeafClone);
                        break;
                    case 'level':
                        const gp = new THREE.Group();
                        obj.rotation.y = - PI / 2;
                        gp.add(obj);
                        obj = gp;
                        break;
                    default:
                        break;
                }
                positionList.forEach((p, i) => {
                    const model = obj.clone();
                    // 向量夹角
                    let rad = this.calcVectorAngle({ x: p.v.x, y: 0, z: p.v.z }, { x: 0, y: 0, z: 1 });
                    // 平移向量
                    const hori = this.calcOrthVector({ x: 0, y: 1, z: 0}, p.v, PI / 2, true);
                    const radPM = p.v.x > 0 ? true : false; // 旋转角正负
                    
                    if (!radPM) rad = -rad;
                    model.rotation.y = rad;

                    if (twice) { //左右各一个模型
                        const transR = this.vectorAdd(p.p, this.vectorNumMultiply(hori, twice));
                        transR.y += (offsetY || 0);
                        const transL = this.vectorAdd(p.p, this.vectorNumMultiply(hori, -twice));
                        transL.y += (offsetY || 0);
                        const model2 = model.clone();
                        model.position.set(transR.x, transR.y, transR.z);
                        model2.position.set(transL.x, transL.y, transL.z);
                        group.add(model);
                        group.add(model2);
                        if (objName === 'fan-shell') {
                            this.fanAnim(model);
                            this.fanAnim(model2);
                        }
                        this.addDownModel([model, model2], group, objName);
                    } else {
                        let { x, y, z } = p.p;
                        if (offsetX) {
                            const v = this.vectorAdd(p.p, this.vectorNumMultiply(hori, offsetX));
                            x = v.x;
                            y = v.y;
                            z = v.z;
                        }
                        model.position.set(x, y + (offsetY || 0), z);
                        if (objName === 'level') {
                            // console.log(levelCode);
                            if (!horizontal) {
                                horizontal = model.position.y - 3.2;
                            } else {
                                if (model.position.y + 2.8 <= horizontal) { // 到达水平仪回落点
                                    this.addDoubleLevel(group, model, horizontal);
                                    horizontal = model.position.y - 3.2;
                                } else if (model.position.y -3.2 >= horizontal) { // 到达水平仪提升点
                                    this.addDoubleLevel(group, model, horizontal);
                                    horizontal = model.position.y + 2.8;
                                }
                            }
                            model.position.y = horizontal;
                            const levelData = this.levelList[i];
                            // 水平仪位置
                            levelData.levelP = model.position;
                            // 相机位置
                            const cp = this.vectorAdd(model.position, this.vectorNumMultiply(p.v, 2));
                            levelData.cameraP = cp;
                            levelData.referP = { x, y, z };
                        }
                        group.add(model);
                        this.addDownModel([model], group, objName);
                    }
                });
                this.tunnelGroup.add(group);
            });
        },
        addDoubleLevel(group, model, horizontal) { // 生成双水平仪对象
            const levelClone = model.clone();
            levelClone.position.y = horizontal;
            group.add(levelClone);
        },
        addDownModel(modelList, group, objName) { // 复制生成下层对象
            if (objName === 'level') return;
            modelList.forEach((model) => {
                const copy = model.clone();
                copy.position.y -= 4.7;
                if (objName === 'fan-shell') {
                    this.fanAnim(copy);
                }
                group.add(copy);
            });
        },
        fanAnim(target) { // 风机旋转动画
            const o = { r: 0 };
            const rotation = new TWEEN.Tween(o)
                .to({ r: PI * 2 }, 1000)
                .onUpdate((obj) => {
                    target.children[1].children[0].rotation.z = obj.r;
                    target.children[2].children[0].rotation.z = obj.r;
                })
                .repeat(Infinity);
            rotation.start();
        },
        createSigns(index) { // 160 95
            const geom = this.initGeometry('Box', 3.6, 2.15, 0.1);
            const url = require(`@/assets/imgs/signs-${index+1}.jpeg`);
            const texture = this.loadTexture(url);
            const mat = this.initMaterial('MeshLambert', { color: 0xffffff, transparent: true });
            const picMat = new THREE.MeshLambertMaterial({ map: texture, transparent: true });
            const materialArr = [];
            for (let i = 0; i < 6; i++) {
                if (i === 5) {
                    materialArr.push(picMat);
                } else {
                    materialArr.push(mat);
                }
            }
            const sign = new THREE.Mesh(geom, materialArr);
            sign.scale.set(-1, -1, -1);
            sign.position.set(-6.5, 9.5, 0);
            return sign;
        },
    }
}