<template>
    <div class='container'>
        <div class='loading' v-if='isLoading'>
            <img :src='require("../assets/imgs/loading.png")' />
        </div>
        <div v-show='levelDetailSwitch' class='tips'>水平仪<span>{{levelDetail.code}}</span>, 距离地面<span>{{levelDetail.hd}}M</span></div>
        <div class='control'>
            <div class='control-camera'>
                <div :class='{"selected": cameraType === "free"}' @click='changeCameraType("free")'>自由</div>
                <div :class='{"selected": cameraType === "top"}' @click='changeCameraType("top")'>俯视</div>
                <div :class='{"selected": cameraType === "side"}' @click='changeCameraType("side")'>正视</div>
            </div>
            <div class='control-level'>
                <ul>
                    <li @click='selectLevel(item)' v-for='item in levelList'>{{item.code}}</li>
                </ul>
            </div>
        </div>
        <div ref='stats'></div>
        <div ref='canvas' id='WebGL-output'></div>
    </div>
</template>

<script>
import math from '../mixins/math.js';
import mixin from '../mixins/threeMixin.js';
import loadModel from '../mixins/loadModel.js';
import calcjs from '../mixins/calc.js';
import P from '../mixins/params.js';

const { PI, cos, sin, floor } = Math;
const { THREE, TWEEN } = window;
const mainGroup = new THREE.Group();
mainGroup.name = 'mainGroup';
const tunnelGroup = new THREE.Group();
tunnelGroup.name = 'tunnelGroup';
export default {
    mixins: [calcjs, mixin, math, loadModel],
    data() {
        return {
            prevTarget: null,
            cameraType: 'free',
            controlSwitch: true,
            data: { // 隧道相关模型参数
                totalLength: 0, // 模型总长
                coordArr: [], // 坐标数组，管理当前距离及位置和向量等信息
            },
            cameraPosition: {
                free: { x: 560, y: 17, z: 1174 },
                top: { x: 0, y: 3000, z: 0 },
                side: { x: 1000, y: 0, z: -800 },
            },
            isLoading: false,
            levelList: [],
            levelDetail: {},
            levelDetailSwitch: false,
        };
    },
    methods: {
        initData() {
            for (let i = 3600; i <= 6400; i += 50) {
                const str = i.toString();
                const code = `DK${str.substr(0,1)}+${str.substr(1)}`;
                this.levelList.push({ code });
            }
        },
        initWebGL() {
            this.isLoading = true;
            const { scene, camera, renderer } = this.initBasics(this.$refs.canvas, {
                position: { x: 560, y: 17, z: 1174 }, antialias: true
            }, {
                clearColor: 0x1F243A, shadowEnabled: true
            });
            this.scene = scene;
            this.camera = camera;
            this.renderer = renderer;
            this.scene.add(mainGroup);
            mainGroup.add(tunnelGroup);
            this.tunnelGroup = tunnelGroup;

            // this.addAxes(1000);
            // const stats = this.initStats(this.$refs.stats);

            const control = this.addControl();
            this.cameraControl = control;
            const clock = new THREE.Clock();

            this.addLight();
            this.addModel();

            const renderScene = () => {
                // stats.update();
                // console.log(this.camera.position);
                TWEEN.update();
                if (this.controlSwitch) {
                    const delta = clock.getDelta();
                    control.update(delta);
                }
                requestAnimationFrame(renderScene);
                renderer.render(scene, camera);
            };
            renderScene();
        },
        changeCameraType(type) {
            this.levelDetailSwitch = false;
            switch (type) {
                case 'free':
                    this.controlSwitch = true;
                    // this.cameraControl.lookAt(100, 0, 0);
                    break;
                case 'top':
                    this.controlSwitch = false;
                    break;
                case 'side':
                    this.controlSwitch = false;
                    break;
                default:
                    return;
            }
            const { x, y, z } = this.cameraPosition[type];
            this.camera.position.set(x, y, z);
            this.camera.lookAt(0, 0, 0);
            this.cameraType = type;
        },
        async addModel() {
            // 隧道
            const tunnel = this.loadModel('suidao');
            tunnel.then((obj) => {
                mainGroup.add(obj);
                this.isLoading = false;
            });

            // 基准轴线
            const axis = await this.loadModel('xian');
            const axisGeom = new THREE.Geometry().fromBufferGeometry(axis.children[0].geometry);
            axisGeom.mergeVertices();
            this.handleAxisData(axisGeom.vertices);

            // 信号灯
            this.addObject('signal', {
                num: 5,
                interval: this.data.totalLength / 6,
                offsetY: 4.8,
                twice: 1.5,
                start: 0,
                end: this.data.totalLength,
            });

            // 情报板
            this.addObject('board', {
                num: 2,
                interval: this.data.totalLength / 3,
                intervalOffset: this.data.totalLength / 12,
                offsetY: 4.7,
                scale: 0.5,
                start: this.data.totalLength / 12,
            });

            // 限速标志
            this.addObject('speedLimit', {
                num: 2,
                start: this.data.totalLength / 4,
                interval: this.data.totalLength / 3,
                intervalOffset: this.data.totalLength / 4,
                offsetY: 4.7,
                scale: 0.0008,
                twice: 1.5,
            });

            // 隧道照明
            this.addObject('lamp', {
                num: 47,
                interval: this.data.totalLength / 50,
                offsetY: 4.8,
            });

            // 监控
            this.addObject('camera', {
                num: 17,
                interval: this.data.totalLength / 18,
                offsetX: 4,
                offsetY: 4.5,
                scale: 0.002,
            });

            // 消防栓
            this.addObject('hydrant', {
                num: 13,
                interval: this.data.totalLength / 14,
                offsetX: -4.6,
                offsetY: 1.8,
                scale: 0.5,
            });

            // 风机
            this.addObject('fan-shell', {
                num: 3,
                interval: this.data.totalLength / 3.5,
                offsetY: 4.4,
                twice: 1.2,
            });
            // 水平仪，57个节点，共62个。
            this.addObject('level', {
                num: 56,
                interval: this.data.totalLength / 59,
                // offsetY: -3.2, // ;2.8
                offsetX: -5.3,
                scale: 0.002,
                start: 2,
            });
        },
        addLight() {
            const ambientLight = this.initLight('Ambient', {color: 0xffffff});
            const target = this.initTarget(0, 0, 0);
            const directionalLight = this.initLight('Directional', {
                color: 0x919191,
                position: {x: 0, y: 50, z: 0},
                castShadow: true,
                shadow: {
                    camera: {
                        near: 2,
                        far: 100,
                        left: -650,
                        right: 650,
                        top: 1300,
                        bottom: -1300,
                    },
                    mapSize: {
                        width: 2048,
                        height: 2048,
                    },
                },
            });
            directionalLight.target = target;
            // const helper = new THREE.CameraHelper(directionalLight.shadow.camera);
            // this.scene.add(helper);
            mainGroup.add(ambientLight);
            mainGroup.add(directionalLight);
        },
        selectLevel(levelData) {
            this.cameraType = '';
            this.controlSwitch = false;
            this.levelDetailSwitch = true;
            const { x: cx, y: cy, z: cz } = levelData.cameraP;
            const { x: lx, y: ly, z: lz } = levelData.levelP;
            const refer = levelData.referP.y - 3.5;
            // 水平仪离地面高度
            let hd = (ly - refer) * 1.2;
            if (hd < 0.75) hd = 0.75;
            if (hd > 7.35) hd = 7.35;
            this.camera.position.set(cx, cy, cz);
            this.camera.lookAt(lx, ly, lz);
            this.levelDetail = {
                code: levelData.code,
                hd: hd.toFixed(2),
            };
        },
        addAxes(len) { // 默认辅助坐标系
            const axes = this.initAxes(len);
            this.scene.add(axes);
        },
        addControl() { // 摄影机控制器
            const control = this.initControls('FirstPerson', this.camera, this.renderer.domElement, {
                lookSpeed: 0.1,
                movementSpeed: 50,
                noFly: true,
                lookVertical: true,
                constrainVertical: true,
                verticalMin: 1.0,
                verticalMax: 2.0,
                lon: -50,
                lat: 50,
            });
            // const control = this.initControls('Trackball', this.camera, this.renderer.domElement, {
            //     rotateSpeed: 1.0,
            //     zoomSpeed: 1.0,
            //     panSpeed: 1.0,
            // });
            return control;
        },
    },
    mounted() {
        this.initData();
        this.initWebGL();
    },
}
</script>

<style lang='less' scoped>
.loading {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background: rgba(255,255,255,0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 99;

    img {
        width: 1rem;
        animation: rotate 2s infinite linear;
    }
    @keyframes rotate {
        from {
            transform: rotate(0deg);
        }
        to {
            transform: rotate(360deg);
        }
    }
}
.tips {
    position: fixed;
    width: 4rem;
    height: 0.7rem;
    border-radius: 5px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 50%;
    top: 10%;
    transform: translateX(-50%);

    span {
        color: red;
    }
}
.control {
    position: fixed;
    top: 0;
    left: 0;
    &-camera {
        display: flex;
        width: 2.5rem;
        height: 0.4rem;
        margin: .1rem 0 0 .1rem;
        overflow: hidden;
        div {
            display: flex;
            flex-grow: 1;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            border: 1px solid #d9d9d9;
            background: #fff;
            &:first-child {
                border-radius: 5px 0 0 5px;
            }
            &:last-child {
                border-radius: 0 5px 5px 0;
            }
        }
        .selected {
            color: #1890ff;
            border-color: #1890ff;
        }
    }

    &-level {
        width: 1.5rem;
        height: 8rem;
        margin: .1rem;
        border-radius: 5px;
        overflow: hidden;
        background: #fff;

        ul {
            width: 100%;
            height: 100%;
            overflow-y: auto;

            li {
                height: .5rem;
                display: flex;
                justify-content: center;
                align-items: center;
                border-bottom: 1px solid grey;
                cursor: pointer;

                &:last-child {
                    border-bottom: none;
                }
            }
        }
    }
}
</style>